@charset "UTF-8";

.btn-email {
    background: #dfe3e8;
    color: #0b1d24;
    border: 3px solid #f5cc7c;
    border-radius: 40px;
    padding: 5px 25px;
    font-family: Poppins, sans-serif;
    font-size: calc(18px + (20 - 18) * ((100vw - 600px) / (1600 - 600)));
    &:hover {
        opacity: 0.7;
        transition: all 0.3s ease;
    }
}

.contact-text {
    font-size: 20px;
    margin-bottom: 5vh;
    margin-top: 5vh;
    font-family: Josefin Sans, sans-serif;
}

.contact-container {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.title-wrapper {
    text-align: center;
    .title {
        font-size: calc(42px + (66 - 42) * ((100vw - 600px) / (1600 - 600)));
        margin-bottom: -25px;
    }
    .title-line {
        width: calc(95px + (127 - 95) * ((100vw - 600px) / (1600 - 600)));
    }
}

.contact-icon {
    height: 5vh;
    width: 5vh;
}

.contact-line-left,
.contact-line-right {
    height: 0.3vh;
    width: auto;
}

.contact-icons-bar {
    width: 100vw;
    padding-top: 20%;
    margin-left: -10%;
}

@media all and(max-width:800px) and (min-width: 600px) {
    .contact-icons-bar {
        width: 100vw;
        padding-top: 20%;
        margin-left: -1%;
    }
}
